html,
body {
  height: 100%;
  margin: 0;
}
.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background: radial-gradient(circle at center, #66bade, #d0e994); */
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
}
.app_colorpicker_container {
  background-color: rgb(255, 255, 255);
  width: 50%;
  min-height: 200px;
  border-radius: 20px;
  box-shadow: 2px 2px 20px rgba(0, 0, 0, 1);
  display: flex;
  justify-content: center;
  align-items: center;
}
